.About{
    display: flex;
    justify-content: space-between;
    transition: all 200ms ease-in-out;
    align-items: center;
}

.About-left{
    position: relative;
    top:300px;
    opacity: 0;
    max-width: 60%;
    text-align: justify;
    transition: top 750ms ease, opacity 750ms ease;
}
.about-img{
    opacity: 0;
    position: relative;
    right:-600px;
    height:420px;
    width: 420px;
    // animation: aboutImageAnimtion 1s linear;
    // animation-fill-mode: forwards;
    transition: right 750ms ease, opacity 750ms ease;
}
@keyframes aboutImageAnimtion {
    from{
        right:-600px;
    }
    to{
        right:0px;
    }
}
.about-img>img{
    height: 100%;
}
@media screen and (max-width:1150px){
    .About{

        max-width: 90vw;
        align-items: center;
    }
    .about-img{
        height: 30vw;
        width: 30vw; 
    }
}

@media screen and (max-width:900px){
    .About{
        flex-direction: column-reverse;
    }
    .About-left{
        max-width: 700px;
        margin-bottom:2rem;
        text-align: center;

    }
    .about-img{
        width: 700px; 
        height:  700px;
        margin-bottom:3rem;
    }
}
@media screen and (max-width:800px){
   
    .About-left{
        max-width: 85vw;
        text-align: start
    }
    .about-img{
        width: 85vw; 
        height:  85vw;
    }
}