$white:#ffffff;
$main-blue:linear-gradient(90deg, #fcff9e 0%, #c67700 100%);

.MintSection{
    width: 100vw;
    background-color:  rgb(58, 55, 32);
}

.MintSection-left-bottom{
    display: flex;
    margin-top: 20px;
}

.MintSection-bossNum-top,.MintSection-bossNum-bottom{
    margin:0 0 20px 0;
    display: flex;
  
    height: 55px;
    width:300px;
    justify-content: space-between;
}
.MintSection-bossNum-bottom{
    margin:0;
    width: 300px;
}
.MintSection-bossNum-top>input{
    height: 51px;
    border:2px solid $main-blue;
    outline:none;
    background: transparent;
    max-width: 55%;
    text-align: center;
    border-radius: 12px;
}
.mint-plus-btn,.mint-button,.mint-amount-btn{
    height: 100%;
    aspect-ratio: 1;
    border:2px solid $main-blue;
    border-radius: 12px;
    cursor: pointer;
    background: transparent;
}

.mint-plus-btn:hover{
    background:  $main-blue;
    color:black;
}

.mint-button{
    height: 55px;
    width:300px;
    background:  $main-blue;
}
.mint-button:hover{
    background: transparent;
    color:$white;
}
.mint-amount-btn{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:30%;
    height:85%;
}
.mint-amount-btn:hover{
    background: $main-blue;
    color:black;
}
.mint-amount-btn>img{
    height:78%;
}

.MintSection-inner{
    display: flex;
    position: relative;
    justify-content: space-between;
    background-color: rgb(58, 55, 32);
    width:1110px;
    height: 100vh ;
    max-height: calc(100vh - 100px);
    box-sizing: border-box;
    transition: all 200ms ease-in-out;
    margin: auto;
}
.MintSection-left{
 
    width: 35%;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    
    height: 100%;
}
.MintSection-right{
    display: flex;
    align-items: center;
  
    height: 100%;
    width:65%;
    img{
        width: 108%;
        @media screen and (min-width:1600px) {
            width: 135%;
        }
    }
}

@media  screen and (max-width:1150px) {
    .MintSection-inner{
        width:90vw;
    }
    .MintSection-right{
        img{
            width: 100%;
            
        }
    }
}

@media  screen and (max-width:880px) {
    .MintSection-inner{
        width:90vw;
        flex-direction: column-reverse;
        height: fit-content;
        align-items: center;
        max-height: none;
    }
    .MintSection-left{
        
        width: 300px;
        margin-bottom: 4rem;
        margin-top: 2rem;
    }
    .MintSection-right{
        
        width: 90%;
        img{
            width: 100%;
            
        }
    }
}