.App {
  position: relative;
  background-color: black;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-tap-highlight-color: transparent;
}
.App-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: animation 2s ease-in-out;
}
@keyframes animation {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
} 


.bg-image-holder{
  display:flex;
  align-items:center;
  justify-content:center;
  height: 90vh;
  width: 100vw;
  overflow: hidden;
  z-index:0;
  // background-image: url(./assets/images/header.png);
  background-position: center;
  background-size:cover;
}

@media screen and (max-width:700px){
  .bg-image-holder{
   
    // height: calc(100vw * 0.3875);
    height: 70vw;
    
  }
}

.bg-mint-pass-image{
  width: 100vw;
  height: calc(100vw * 0.3875);
  background-image: url(./assets/images/vip-pass.svg);
  background-size:contain;
}
