.TeamSection{
    display:flex;
    position: relative;
    justify-content: center;
    width: 100%;
    background-color:black;
    z-index: 10;
    
}
.TeamSection-inner{
    

    width:100%;
    background-color: #000;
    padding: 60px 0 60px 0;
    width:1110px;
}
.TeamSection-inner-bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    width: 100%;
 
}
.Team-wrapper{
    display: grid;
    text-align: center;
    height: 360px;
    width: 22%;
    opacity: 0;
    position: relative;
    top:300px;
    // transition: top 750ms ease, opacity 750ms ease;
}
#team-wrapper-1{
    transition: top 700ms ease, opacity 700ms ease;

}
#team-wrapper-2{
    transition: top 900ms ease, opacity 900ms ease;

}
#team-wrapper-3{
    transition: top 1.1s ease, opacity 1.1s ease;

}
#team-wrapper-4{
    transition: top 1.2s ease, opacity 1.2s ease;

}
.Team-img{
    width:100%;
    border-radius: 25px;
}
@media screen and (max-width:1120px) {
    .TeamSection-inner{
    

        width:85vw;
        background-color: #000;
        padding: 60px 0 60px 0;
      
    }
    .TeamSection-inner-bottom{
        display: flex;
       
       
        // height: fit-content;
        width: 100%;
        flex-wrap: wrap;
        // background-color: red;
        // padding: 0 3rem 0 3rem;
        // box-sizing: border-box;
    }
    .Team-wrapper{
        display: grid;
        text-align: center;
        height: 350px;
        width: 200px !important;
        margin:1.5rem
    }
}
@media screen and (max-width:585px) {
    .TeamSection-inner-bottom{
    
        flex-direction: column;
     
    }
    .Team-wrapper{
     
        height: fit-content;
       
        margin-top: 2rem;
    }
}