#roadmap{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // background-color: red;
}
.roadmap-inner{
    display: flex;
    height: 450px;
    width: 1110px;
    justify-content: space-between;
}
.roadmap-left{
    position: relative;
    left:-600px;
    opacity: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    // p{
    //     max-width: 90%;
    //     margin-top: 0.2rem;
    // }
    transition: left 750ms ease, opacity 750ms ease;
}
.roadmap-right{
    opacity: 0;
    position: relative;
    top:300px;
    height: 450px;
    
    img{
        height: 100%;
        border-radius: 25px;
    }
    transition: top 750ms ease, opacity 750ms ease;

}
.roadmap-progress{
    position: absolute;
    height: 100%;
    width: 12px;
    // display: flex;
    // flex-direction: column;
    // background-color: blue;
    overflow: hidden;
    border-radius: 25px;
    background:linear-gradient(90deg, #fcff9e 0%, #c67700 100%) ;
    // img{
    //     height: 25.5%;
    //     margin-top: -4px;
    //     padding: 0px;
        
    // }
    
}
.roadmap-section{
    display: flex;
    align-items: center;
 
    margin-top: 0.5rem;
    // position: relative;
    p{
        margin-left: 40px;
    }
    img{
        position: absolute;
        height: 30px;
        margin-left: -19px;
        // margin-top: auto;
        // margin-bottom: auto;
        // top:0;
        // bottom: 0;
    }
}
