@media  screen and (max-width:1130px) {
    .roadmap-inner{
       
        height: 500px;
        width: 90vw;
    }
}
@media  screen and (max-width:900px) {
    .roadmap-inner{
        flex-direction: column;
        height: fit-content;
        width: 85vw;
    }
    .roadmap-left{
        p{
            margin-top: 1rem;
            max-width: 100%;
        }
    }
    .roadmap-right{
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 4rem;
    }
}

@media  screen and (max-width:470px) {
 
    .roadmap-right{
        height: 85vw;
    }
}
